/*Chrome*/
@media screen and (-webkit-min-device-pixel-ratio:0) {
  input[type='range'] {
    overflow: hidden;
    /* width: 80px; */
    -webkit-appearance: none;
    /* background-color: #33363b; */
    margin: 10px;
    border-radius: 10px;
  }
  
  input[type='range']::-webkit-slider-runnable-track {
    height: 10px;
    /* -webkit-appearance: none; */
    color: #739c62;
    border-radius: 10px;
    /* margin-top: -1px; */
  }
  
  input[type='range']::-webkit-slider-thumb {
    width: 10px;
    -webkit-appearance: none;
    height: 10px;
    border-radius: 50%;
    cursor: pointer;
    background: #207bbd;
    box-shadow: -80px 0 0 80px #207bbd;
  }
}

