@media (min-width: 993px) {
  .sidebar {
    height: 100vh;
    margin: 0px;
    background-color: #33363b;
    color: #eaeaea;
    z-index: 1;
  }

  .logo {
    width: 50px;
    height: 50px;
    border: solid #fff;
    border-radius: 50%;
    margin: 20px;
  }

  .expand {
    display: none;
  }

  .sidebar ul {
    list-style: none;
    text-align: left;
    margin: 0px;
    padding: 0px;
  }

  .sidebar__Link--active li {
    border-top: 1px solid #eaeaea;
    border-bottom: 1px solid #eaeaea;
    background-color: #207bbd;
  }

  .tab:hover {
    animation-name: hover;
    animation-duration: .2s;
    background-color: rgb(54, 140, 191, 0.5);
  }

  .anchor {
    position: absolute;
    bottom: 0;
    width: 100%;
    padding-bottom: 40px;
  }

  .header {
    padding: 26px;
    background-color: #33363b;
    color: #eaeaea;
  }

  .devicons-list {
    font-size: 100px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,  -50%);
  }

  .cancel {
    display: none;
  }

  .skills-list {
    text-align: left;
    font-size: 26px;
    position: relative;
    top: 50%;
    transform: translate(0,  -50%);
  }

  .skill {
    padding: 15px !important;
    min-height: 20vh;
  }

  .project-list {
    text-align: left;
    font-size: 26px;
    position: relative;
    top: 50%;
    transform: translate(0,  -50%);
  }
}

@media (max-width:992px) {
  .bar1, .bar2, .bar3 {
    width: 35px;
    height: 5px;
    background-color: #eaeaea;
    margin: 6px 0;
    transition: 0.4s;
  }
  
  .hide .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
  }
  
  .hide .bar2 {opacity: 0;}
  
  .hide .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
  }

  .sidebar {
    margin: 0px;
    background-color: #33363b;
    color: #eaeaea;
    z-index: 1;
    height: 11vh;
  }

  .sidebar .expand {
    /* background-color: #eaeaea; */
    position: absolute;
    /* left: 1%; */
    margin: 20px;
  }

  .sidebar .hide {
    /* background-color: #eaeaea; */
    position: absolute;
    /* left: 1%; */
    margin: 20px;
  }

  .expanded {
    /* height: 100%; */
    /* width: 35%; */
    z-index: 1;
    left: 0;
    background-color: #111;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
    top: 11vh;
  }

  .collapsed {
    height: 0;
    /* width: 0; */
    z-index: 1;
    left: 0;
    background-color: #111;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
  }

  .logo {
    width: 50px;
    height: 50px;
    border: solid #fff;
    border-radius: 50%;
    margin: 20px;
  }
  
  .icon img:hover {
    border: solid #207bbd;
  }

  .sidebar ul {
    position: absolute;
    /* height: 100vh; */
    background-color: #33363b;
    z-index: 2;
    list-style: none;
    text-align: left;
    margin: 0px;
    padding: 0px;
    /* top: 13vh;
    height: 100vh; */
  }

  .sidebar__Link--active li {
    border-top: 1px solid #eaeaea;
    border-bottom: 1px solid #eaeaea;
    background-color: #207bbd;
  }
  .anchor {
    display: none;
  }

  .cancel {
    text-align: right;
    padding-right: 20px;
  }

  .header {
    display: none;
  }

  .about {
    overflow-y: scroll;
  }

  .devicons-list {
    font-size: 50px;
  }

  .skills-list {
    text-align: left;
    font-size: 26px;
  }
  
  .name-section {
    height: 40px;
  }

  .skill {
    padding: 15px !important;
  }

  .project-list {
    text-align: left;
    font-size: 26px;
  }

  .resume-pdf {
    overflow-y: scroll;
    overflow-x: auto;
    height: 100%
  }

  .doc-body {
    height: 100%
  }

  .project-content {
    margin-top: 20px;
  }
}

